<template>
    <div class="login">
        <el-row class="login-content">
            <el-col :span="12" class="content-left">
                <h3>还没有账号？</h3>
                <h3>立即免费注册</h3>
                <p>Wasting time is robbing oneself.</p>
                <div class="left-button"><el-button type="primary" size="small" @click="isLogin=!isLogin">注 册</el-button></div>
            </el-col>
            <el-col :span="12" class="content-right">
                <h2>Nothing is impossible</h2>
                <p>Everyone is talented.</p>
                <p>We want you and you can be!</p>
                <p>你就是你，独一无二</p>
                <div class="right-button"><el-button type="primary" size="small" @click="isLogin=!isLogin">登 录</el-button></div>

            </el-col>
            <div class="login-form" :style="{left:isLogin?'290px':'35px'}">
                <div class="title"><h2>{{isLogin?'登 录':'注 册'}}</h2></div>
                <transition :duration="1000" enter-active-class="animated zoomIn"
                            leave-active-class="animated zoomOut">
                <el-form class="formPosition" key="login" v-if="isLogin" size="small" ref="loginForm" :rules="loginRules" :model="loginForm" >
                    <el-form-item class="form" prop="email">
                        <el-input  v-model="loginForm.email">
                            <template slot="prepend"><i class="iconfont icon-mail"></i></template>
                        </el-input>
                    </el-form-item>
                    <el-form-item class="form" prop="password">
                        <el-input type="password"  v-model="loginForm.password">
                            <template slot="prepend"><i class="iconfont icon-lock"></i></template>
                        </el-input>
                    </el-form-item>
                    <el-form-item class="form" prop="code">
                        <el-row>
                            <el-col :span="6">
                                <el-input  v-model="loginForm.code">
                                </el-input>
                            </el-col>
                            <el-col :span="8" :offset="1">
                                <el-input  disabled>
                                </el-input>
                            </el-col>
                            <el-col :span="8" :offset="1" class="loginPwd">
                               <!--<span>忘记密码</span>-->
                                <el-checkbox v-model="isRemember" class="checkboxRemember">记住密码</el-checkbox>
                            </el-col>
                        </el-row>

                    </el-form-item>
                    <div class="form">
                        <el-button size="small" class="submit" type="primary" @click="loginSubmit">登 录</el-button>
                    </div>
                    <div class="form">
                        <el-button size="mini" type="text" @click="isLogin=!isLogin">还没有账号？立即注册</el-button><span>忘记密码</span>
                    </div>
                </el-form>
                <el-form class="formPosition" key="register" v-if="!isLogin" size="small" ref="registerForm" :rules="registerRules" :model="registerForm" >
                            <el-form-item class="form" prop="email">
                                <el-input  v-model="registerForm.email">
                                    <template slot="prepend"><i class="iconfont icon-mail"></i></template>
                                </el-input>
                            </el-form-item>
                    <el-form-item class="form" prop="password">
                        <el-input type="password"  v-model="registerForm.password">
                            <template slot="prepend"><i class="iconfont icon-lock"></i></template>
                        </el-input>
                    </el-form-item>
                    <el-form-item class="form" prop="code">
                        <el-row>
                            <el-col :span="13">
                                <el-input  v-model="registerForm.code">
                                    <template slot="prepend"><i class="iconfont icon-key"></i></template>
                                </el-input>
                            </el-col>
                            <el-col :span="8" :offset="2">
                                <el-button class="btn-getCode" type="primary">获取验证码</el-button>
                            </el-col>
                        </el-row>

                    </el-form-item>
                    <div class="form">
                        <el-button size="small" class="submit" type="primary">注 册</el-button>
                    </div>
                    <div>
                        <el-button size="mini" type="text" @click="isLogin=!isLogin">已有账号，立即登录</el-button>
                    </div>
                </el-form>
                </transition>
            </div>
        </el-row>
    </div>
</template>

<script>
export default {
  name: "login",
  data() {
    let codeValidator = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("请输入验证码！"));
      }
    };
    return {
      isRemember: false,
      loginForm: {
        email: "",
        password: "",
        code: ""
      },
      loginRules: {
        email: [
          { required: true, message: "请输入邮箱地址", trigger: "blur" },
          {
            type: "email",
            message: "请输入正确的邮箱地址",
            trigger: ["blur", "change"]
          }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 6,
            max: 16,
            message: "请输入6~12位密码",
            trigger: ["blur", "change"]
          }
        ]
      },
      isLogin: false,
      registerForm: {
        email: "",
        password: "",
        code: ""
      },
      registerRules: {
        email: [
          { required: true, message: "请输入邮箱地址", trigger: "blur" },
          {
            type: "email",
            message: "请输入正确的邮箱地址",
            trigger: ["blur", "change"]
          }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 6,
            max: 16,
            message: "请输入6~12位密码",
            trigger: ["blur", "change"]
          }
        ],
        code: [{ validator: codeValidator, trigger: "blur" }]
      }
    };
  },
  methods: {
    loginSubmit() {
      this.$router.push("/blog/list");
    }
  }
};
</script>

<style scoped>
</style>
